$blue: #434D66;
$dark-blue: #191D29;
$green: #38ABA3;
$white: #ECEBF3;
$brown: #B98B02;
$yellow: #F1C711;
$orange: #EE6C4D;

.contact {
    height: 80%;
    width: 50%;
    text-align: center;
    display: block;
    margin: 4rem auto 8rem auto;
    background: rgba(25,29,41,0.5);
    border: solid 1px $orange;
    border-radius: 20px;
    padding: 1rem 2rem;

    .contact-container {
        margin: 5rem;

        .alertOk{
            top: 0;
            margin: auto;
            font-size: 1.5rem;
            text-transform: uppercase;
            color: $orange;
            border: solid 2px $orange;
            max-width: 300px;
            margin-top: -3rem;
            transition: all 0.5s ease-in-out;
        }
            
        h4{
            text-align: left;
            color: $green;
            font-size: 1.5rem;
            margin-bottom: 3rem;
            margin-top: 2rem;
        }
    
        .contact-form {
            height: 100px;
            display: block;

            .form-input{
                border: none;
                height: 2rem;
                padding: 0.5rem;
                font-size: 1.2rem;
                background-color: $blue;
                width: 45%;
                color: $green;
                outline: none;
            }

            .input-email {
                margin-left: 5%;
            }

            .form-textarea {
                outline: none;
                width: 100%;
                height: 200px;
                padding: 0.5rem;
                font-size: 1.2rem;
                box-sizing: border-box;
                background-color: $blue;
                color: $green;
                border: none;

            }
        }
    }
}

@media (max-height: 700px) {

    .contact {
        .contact-container{
            margin: 0;
            padding: 1rem;
            .contact-form{
                display: block;

                .form-input{
                    display: block;
                    width: 98%;
                    margin: auto;
                    margin-bottom: 1rem;
                }

                .form-textarea {
                    margin-top: -3rem;
                }
            }

        }
    }
}

@media (max-width: 1750px) {

    .contact {
        .contact-container{
            margin: 0;
            padding: 1rem;
            .contact-form{
                display: block;

                .form-input{
                    display: block;
                    width: 98%;
                    margin: auto;
                    margin-bottom: 1rem;
                }

                .form-textarea {
                    margin-top: -3rem;
                }
            }

        }
    }
}


@media screen and (max-width: 420px) {
    .contact{
        margin: 0 1rem;
        display: block;
        width: auto;
        padding: 0;
        box-sizing: border-box;
        height: 90%;
        margin-bottom: 4rem;
        .contact-container{
            margin: 0.5rem;
            h4{
                font-size: 1.2rem;
                margin: 1rem auto 2rem auto;
            }

            .contact-form{
                .form-input{
                    font-size: 1rem;
                }
                .form-textarea{
                    font-size: 1rem;
                }
            }
        }
    }
}